@import url(https://fonts.googleapis.com/css?family=Sacramento);
#root,
body,
html {
  height: 100%;
  width: 100%;
}

#app {
  position: relative;
  background: #fff;
  min-height: 100vh;
}

.app-header {
  display: -webkit-flex;
  display: flex;
  background: #fff;
  box-shadow: 0 2px 8px #f0f1f2;
  padding: 0;
}

.app-header__logo-search-section {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.app-header__logo {
  display: inline-block;
  padding: 0 20px;
}

.app-header__logo img {
  width: 36px;
}

.app-header__search-input {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-grow: 1;
          flex-grow: 1;
  padding-right: 10px;
}

.app-header__search-input .ant-input-group {
  border: 0;
  width: 100%;
  max-width: 460px;
}

.app-header__search-input .ant-input-group input {
  border: 1px solid #f0f1f2;
  box-shadow: 0 2px 4px rgba(29, 34, 108, 0.1);
}

.app__affix-header {
  z-index: 99;
}
.home {
  padding: 60px 120px;
  background-repeat: no-repeat;
  background-position: 10% 0;
}

@media (max-width: 75em) {
  .home {
    padding-left: 20px;
    padding-right: 20px;
  }
}

.home__listings {
  padding: 0 0 20px;
}

.home__listings-title.ant-typography {
  color: #1d226c;
  display: inline-block;
}

.home__listing {
  text-align: center;
  margin-bottom: 2em;
  margin-top: 2em;
}

.home__calendar {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
